
import React from 'react'
import { InputGroup, FormControl, Button } from 'react-bootstrap';
import io from '../../io';
import WebRtcTools from '../../webRTCTools';
function getTimeIn24Format() {
  var d = new Date();
  var n = d.toLocaleString([], { hour: '2-digit', minute: '2-digit' });
  return n;
}

function getDate(){
 let date=  new Date();
 console.log(date);
 return date
// return {m:d.getMonth()+1,d:d.getDate(),y:d.getFullYear()}
  
}







class ChatWindow extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      meassge:'',
      meassges:props.chat.meassges,
      whoIsTypeingNow:[],
      myStream:{},
      theirStream:{},      
  }
  this.user=props.user;
  this.chatId=props.chat._id;
  this.newMessageAudio=new Audio('msg.mp3');
  this.el = React.createRef()
  this.myVideoArea = React.createRef();
  this.theirVideoArea = React.createRef();
  this.connectedUser=undefined;
   this.myConnection=undefined;
   this.dataChannel=undefined;
}
componentWillUnmount(){
  io.close('chat'+this.chatId)
  io.close('typeing'+this.chatId)
}

componentDidUpdate() {
    if(this.myConnection){
      console.log(this.myConnection.iceConnectionState)
    }

}
    componentDidMount(){

        
        
      this.createWEBRTCO()
    
          io.on('chat'+this.chatId,(data)=>{
            if(data.senderId!==this.props.user.email) {
              setTimeout(this.scrollToBottom,100);
              this.newMessageAudio.play();
            }
                this.setState({meassges:[...this.state.meassges,data]})
      
          });
          
          io.on('typeing'+this.chatId,(data)=>{
            if(data.show){
              let add=true;
              this.state.whoIsTypeingNow.forEach(name=>name===data.senderName? add=false:null )
            if(add)this.setState({whoIsTypeingNow:[...this.state.whoIsTypeingNow,data.senderName]})
        
            }else {
              this.setState({whoIsTypeingNow:this.state.whoIsTypeingNow.filter(name=>name!==data.senderName)});
        
            }
    
          });
          io.on('connect',data=>{
            console.log('connect')
          })

          io.on('message',(data) =>{ 
           
            switch(data.type) { 
               case "offer": 
               console.log('new offer',data)

               this.onOffer(data.offer, data.name); 
                  break; 
               case "answer": 
               console.log('new answer',data)

               this.onAnswer(data.answer); 
                  break; 
               case "candidate": 
               console.log('new ice candate',data)
               this.onCandidate(data.candidate); 
                  break; 
               default: 
            console.log("Got message", data);

                  break; 
            } 
         })
        
        
        
        
        
      
      
         this.scrollToBottom();
         
        }
        createWEBRTCO=()=>{
               //creating our RTCPeerConnection object 
          
               var configuration = { 
                "iceServers": [{ "url": "stun:stun.1.google.com:19302" }] 
             }; 
           this.myConnection = new RTCPeerConnection(configuration, { 
            optional: [{RtpDataChannels: true}] 
         }); 


         WebRtcTools.getMyDivices('a')
         .then(stream=>{
           this.setState({myStream:  stream});
           this.myVideoArea.srcObject=stream;
          this.myConnection.addStream(stream);          
         })
         .catch(err=>console.log(err));
           console.log("RTCPeerConnection object was created"); 
        
          this.myConnection.onaddstream=(event)=>{
            console.log('got a stream');
            this.theirVideoArea.srcObject=event.stream;

          };

          this.myConnection.onremovestream=e=>{
              console.log('chat is end ',e);
          };


           //setup ice handling
           //when the browser finds an ice candidate we send it to another peer 
           this.myConnection.onicecandidate =  (event) =>{ 
              if (event.candidate) { 
                 this.send({ 
                    type: "candidate", 
                    candidate: event.candidate 
                 }); 
              } 
           }; 
           this.myConnection.ondatachannel = (event)=> {
            var receiveChannel = event.channel;
            receiveChannel.onmessage = (event)=> {
               console.log("ondatachannel message:", event.data);
            };
         };
           this.openDataChannel();
           
        } 
        hangUpCall() { 
          this.connectedUser = null; 
          this.theirVideoArea.srcObject = null; 
         
          this.myConnection.close(); 
          this.myConnection.onicecandidate = null; 
          this.myConnection.onaddstream = null; 
       };
        //setup a peer connection with another user 
        
    setUpOffer= ()=>{
      var otherUsername =  this.getFriendsEmails().email;
      this.connectedUser = otherUsername;
      if (otherUsername.length > 0) { 
         //make an offer 
         this.myConnection.createOffer( (offer) =>{ 
            console.log(); 
            this.send({ 
               type: "offer", 
               offer: offer 
            });
         
            this.myConnection.setLocalDescription(offer); 
         }, function (error) { 
            alert("An error has occurred."); 
         }); 

     
      } 
   


    }
    //when somebody wants to call us 

    onOffer=(offer, name)=> { 
      this.connectedUser = name; 
      this.myConnection.setRemoteDescription(new RTCSessionDescription(offer)); 
      this.myConnection.createAnswer( (answer)=> { 
        this.myConnection.setLocalDescription(answer); 
       
        this.send({ 
          type: "answer", 
          answer: answer 
         }); 
       
        }, function (error) { 
         alert("oops...error"); 
      }); 
   }
   //when another user answers to our offer 

   onAnswer=(answer)=> { 
      this.myConnection.setRemoteDescription(new RTCSessionDescription(answer)); 
 } 
 
 //when we got ice candidate from another user 
  onCandidate=(candidate)=> { 
    this.myConnection.addIceCandidate(new RTCIceCandidate(candidate)); 
 }
 //creating data channel 
 openDataChannel() { 

  var dataChannelOptions = { 
     reliable:false 
  }; 
  console.log('create data chanel')
  this.dataChannel = this.myConnection.createDataChannel("sendDataChannel", dataChannelOptions);
  this.dataChannel.onerror = function (error) { 
     console.log("Error:", error); 
  };
 
  this.dataChannel.onmessage = function (event) { 
     console.log("Got message:", event.data); 
  };  
}
 
//when a user clicks the send message button 


      
  send=(message)=> { 
    if (this.connectedUser) { 
       message.name = this.connectedUser; 
    } 
   
    io.emit(message.type,message); 
 };

 getFriendsEmails=()=>{
   let chat = this.props.chat;
   let friends=[];
   if(chat.type==='friends'){
       Object.values(chat.users).forEach(user=>{
           if(user._id!==this.props.user._id) {
             friends= user;
           }
       });

   }else{
       // group chat
       console.log('group chat ')
   }
     return friends;

}
      
      scrollToBottom = () => {
        this.meassgesEnd.scrollIntoView({ behavior: "smooth" });
      }
    
      addmessage=(message)=>{
        console.log(message)
      }
      onChangeText=(e)=>{
        this.setState({meassge:e.currentTarget.value});
        io.emit('typeing', e.currentTarget.value?{senderName:this.user.firstName,show:true,chatId:this.chatId}:{senderName:this.user.firstName,show:false,chatId:this.chatId});
      }
      
      onEnterPress = (e) => {
        if(e.keyCode === 13 && e.shiftKey === false) {
          this.handlerClickMSG(e);
          
        }
     
      }
      handlerClickMSG=(e)=>{
        e.preventDefault() 
        if(!this.state.meassge) return null
 
        console.log("send message");
        this.dataChannel.send(String(this.state.meassge)); 
      
      
        // this.setState({meassge:''})
        // io.emit('typeing',{senderName:this.user.firstName,show:false,chatId:this.chatId});
        // io.emit('chat',{
        //     message:this.state.meassge,
        //     senderName:this.user.firstName,
        //     senderId:this.user.email,
        //     chatId:this.chatId,
        //     createTime:{time:getTimeIn24Format(), date:getDate()}
            
        // })
      }
    
        renderSomeOneIsTypeing=()=>{
                if(this.state.whoIsTypeingNow.length<1) return null
              else if(this.state.whoIsTypeingNow.length===1){
                      return(
                        <p><em>{this.state.whoIsTypeingNow}</em> is typeing a meassge</p>
                    )
                }
                else {
                  let names ='';
                  this.state.whoIsTypeingNow.map(name=>names= names+' '+ name)
                    return(
                    <p><em>{names}</em> is typeing a meassge</p>
                ) 
                }
              
    }
     
      renderDateOfCreateMessage=(date)=>{
         var d = new Date();
       
        date= new Date(date);
        var numberOfDays =Math.floor((d - date) / (1000*60*60*24))
        switch (numberOfDays) {
          case 0 :
            return 'today'
          case 1:
            return 'Yesterday'            
          case 2:
            return '3 days ago'
          default:

            return `${date.d}/${date.m}/${date.y}`
        }
      }
      renderStyleForTheMessage=(email,style)=>{

       return  email===this.user.email?style: {
  
      }


      }

      renderMessgesList=()=>{
        return this.state.meassges.map((message,index)=>{
       
            return (
              <div key={message.message+index}
                    aria-live="polite"
                    aria-atomic="true"
                    style={{
                      position: 'relative',
                      minHeight: '100px',
                    }}
                  >
              <div style={this.renderStyleForTheMessage(message.senderId,
                {
                  textAlign: 'right',
                  position: 'absolute',
                  top: 0,
                  right: 0,
                  minWidth:'100px'
                })} 
                id='body-message'>
                <div style={this.renderStyleForTheMessage(message.senderId,{backgroundColor:"#b5f66f"})} id='message'>
                    <p id='textMessage' >{message.message}</p>
                </div>
                <small> {`${this.renderDateOfCreateMessage(message.createTime.date)} at ${message.createTime.time } by ${message.senderName}`} </small>
                
              </div>
              </div>
         
            )
        })
      }

      auto_height(elem) {
        elem.style.height = "1px";
        elem.style.height = (elem.scrollHeight)+"px";
    }
    render(){
        return (
            <div id='mario-chat'>
              <div id='chat-window'>
             <div id='videosCon'>
             <audio playsInline ref={ref => this.theirVideoArea = ref} id='theirVideoTAg'   autoPlay > </audio>
             <audio playsInline ref={ref=> this.myVideoArea=ref} id='myVideoTag' autoPlay ></audio>

             </div>
              <div id="feedback">{this.renderSomeOneIsTypeing()}</div>            

              
              <div id='output'>
              <div>

              {this.renderMessgesList()}

              </div>
         
              <div id='hideScrool' style={{ float:"left", clear: "both" }} ref={(el) => { this.meassgesEnd = el; }}> </div>
              </div>
      
              </div>
              <form  onSubmit={this.handlerClickMSG}>
              
              
                <InputGroup id='messageinput'  >
                <InputGroup.Append >
                        <Button onClick={this.setUpOffer}  id='buttonCall' style={{backgroundColor:this.state.meassge?"green":"black"}} variant="outline-secondary">call</Button>
                      </InputGroup.Append>
                    <FormControl onKeyDown={this.onEnterPress}  as="textarea" rows="1" aria-multiline    autoComplete="off" onChange={this.onChangeText} value={this.state.meassge} type='text' placeholder='type a message' />
                      <InputGroup.Append>
                        <Button id='buttonChat' style={{backgroundColor:this.state.meassge?"#575ed8":"#898bce"}} type='submit' variant="outline-secondary"></Button>
                      </InputGroup.Append>
                </InputGroup>



              </form>
            
            </div>
          );
    }
}


export default ChatWindow;